@import url(https://fonts.googleapis.com/css2?family=Ubuntu:wght@500&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Inconsolata:wght@400;500;600&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Roboto+Mono:wght@300;400;500&display=swap);
.profilePage{display:flex;flex-direction:column;align-items:center;justify-content:center;height:100%;width:100%;background-color:#040109}.SearchBar{width:500px;height:65px;padding:0px 5px 0px 19px;font-family:"Inconsolata",monospace;font-weight:600;color:#f2f2f2;font-size:35px;border-radius:3px;background-color:#391372;border:none;outline:none;font-weight:500}.SearchBar:focus{border:none;outline:none}.SearchPage{display:flex;flex-direction:column;align-items:center;justify-content:center;height:100vh;width:100%;background-color:#040109}.SearchPage .searchField{display:flex;flex-direction:column;align-items:center}.SearchPage .searchField__icon{font-size:90px;margin-bottom:30px}.SearchPage .searchField__text{font-size:65px;color:#f2f2f2;font-weight:400;margin-bottom:50px;font-family:"Ubuntu",sans-serif}.profilePage{display:flex;flex-direction:column;align-items:center;justify-content:center;height:100%;width:100%;background-color:#040109}.followedList{display:grid;grid-template-columns:repeat(auto-fit, minmax(250px, 1fr));grid-auto-rows:auto;grid-gap:28px;width:clamp(280px, 80%, 100%);padding:0px 20px}.entry{display:flex;flex-direction:column;background-color:#cafafe;margin:10px;border-start-start-radius:3px;border-start-end-radius:3px;border-end-start-radius:2px;border-end-end-radius:2px}.entry__img{border-start-start-radius:2px;border-start-end-radius:2px;width:100%;height:auto}.entry__info{padding:10px 0px 60px 0px;display:flex;flex-direction:column;align-items:center}.entry__info__login{font-family:"Roboto Mono",monospace;color:#391372;font-size:20px;font-weight:500;padding:0px 0px 10px 0px}p{margin:0px;padding:0px}a{text-decoration:none}*{box-sizing:border-box}html,body,#root{width:100%;height:100%;margin:0;padding:0;color:#8892b0;font-family:'Kanit', sans-serif;-webkit-font-smoothing:antialiased}

